.dynamic-grid {
    display: grid;
    gap: 1rem;
    width: min(85vw, 800px);
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}

.gallery-grid {
    display: grid;
    gap: 1rem;
    /*width: min(85vw, 800px);*/
    /*grid-template-columns: repeat(auto-fit, minmax(min(240px, 30vmin), 1fr));*/
    width: min(80vw, 1050px);
    grid-template-columns: repeat(auto-fit, minmax(min(240px, 30vmin), 1fr));
}

.wide-grid {
    display: grid;
    gap: 1rem;
    width: min(85vw, 1000px);
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.gallery-card, .more-card {
    position: relative;
    min-height: 240px;
    min-width: 240px;
    background-size: cover;
    background-position: center;
    background-image: var(--src);
    opacity: 1;

    transition: transform 0.2s ease, opacity 0.2s ease;
}

.more-card:after {
    content: "View All Services";
    font-family: 'Dancing Script', cursive;
    /*font-family: 'Playfair Display', serif;*/
    color: whitesmoke;
    font-size: 6vmin;
    text-align: center;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: rgba(0, 0, 0, 0.6);
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    padding: 0 1rem;

    cursor: pointer;
}

@media (max-width: 583px) {
    .gallery-card, .more-card {
        min-height: 30vmin;
        min-width: 30vmin;
    }
}

@media (min-width: 980px) {
    .gallery-card:hover {
        transform: scale(1.25);
        z-index: 5;
        -webkit-box-shadow: 0 0 20px 5px #ffffff;
        box-shadow: 0 0 20px 5px #ffffff;
    }

    .more-card:hover {
        transform: scale(0.95);
    }

    .gallery-grid:hover .gallery-card:not(:hover),
    .gallery-grid:hover .more-card:not(:hover) {
        opacity: 0.5;
    }
}


